.field-radio {
  padding: 22px 20px 19px;
  border: none;
  border-bottom: 1px solid #d9d9d9;

  @media (min-width: @tablet-width) {
    padding: 0 39px 0 41px;
    border-bottom: 0;
  }

  @media (min-width: @desktop-width) {
    padding: 0;
  }
}

.field-radio__list {
  margin: 0;
  padding: 0;
  list-style-type: none;

  @media (min-width: @tablet-width) {
    padding: 25px 28px;
    border: 1px solid #d9d9d9;
  }
}

.field-radio__item {
  margin: 0 0 13px;
}

.field-radio__label {
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #222222;

  &--disabled {
    opacity: 0.3;
  }

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 30px;
  }
}

.field-radio__mark {
  margin-right: 15px;
}

.field-radio__input:checked + .field-radio__mark::before {
  content: "";
  position: absolute;
  top: 8px;
  left: 8px;
  width: 13px;
  height: 13px;
  background-color: @base-color;
  border-radius: 50%;
}
